import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

const dialogflowURL = 'http://chatbotapi.ashleybuckingham.co.uk';
@Component({
  selector: 'app-chatbot',
  templateUrl: './chatbot.component.html',
  styleUrls: ['./chatbot.component.css']
})
export class ChatbotComponent implements OnInit {
  messages = [];
  loading = false;

  // Random ID to maintain session with server
  sessionId = Math.random().toString(36).slice(-5);
  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.addBotMessage('Welcome, I am Ashley bot 🤖, I was created by Ashley to look after his portfolio. How can I help you? ');
  }

  handleUserMessage(event) {
    console.log(event);
    const text = event.message;
    this.addUserMessage(text);

    this.loading = true;

    // Make the request 
    this.http.post<any>(
      `${dialogflowURL}/message`,
      {
        "sessionId": this.sessionId,
        "message" : text,
      }
    )
    .subscribe(res => {
      const { fulfillmentText } = res;
      console.log(res[0].queryResult.fulfillmentText);
      this.addBotMessage(res[0].queryResult.fulfillmentText);
      this.loading = false;
    });
  }

  addUserMessage(text) {
    this.messages.push({
      text,
      sender: 'You',
      reply: true,
      date: new Date(),
      user: {
        name: 'Me',
        avatar: 'https://i.gifer.com/no.gif',
      },
    });
  }

  addBotMessage(text) {
    this.messages.push({
      text,
      sender: 'Bot',
      //avatar: '/assets/bot.jpeg',
      date: new Date(),
      user: {
        name: 'Bot',
        avatar: '/assets/images/robot.jpg',
      },
    });
  }

  hide(){
    let chatbox = document.getElementById('myForm') as HTMLElement;
    chatbox.style.display = 'none';
  }

  show(){
    let chatbox = document.getElementById('myForm') as HTMLElement;
    chatbox.style.display = 'block';
  }
}
